<template>
	<view class="content">
		<scroll-view scroll-x class="bg-white nav">
			<view class="flex text-center">
				<view
					class="cu-item flex-sub"
					:class="index == current ? 'text-blue cur' : ''"
					v-for="(item, index) in items"
					:key="index"
					@tap="changeTab(index)"
					:data-id="index"
				>
					{{ item }}
				</view>
			</view>
		</scroll-view>
		<view class="task-box" v-if="dataList.length > 0">
			<view v-for="item in dataList" :key="item.id" class="task-item" style="margin-top: 16rpx;" @click="checkDetail(item)">
				<view class="topTitle">{{ taskTypeOptions[item.taskType] }} {{ item.id }}</view>
				<view class="detail">
					<view class="address">任务名称：{{ item.taskName }}</view>
					<view class="address">巡检内容：{{ item.content }}</view>
				</view>
				<view style="text-align: right;border-top: 1px solid #efefef;padding-top: 24rpx;" v-if="roleId == '5'&&current==0">
					<!-- 中队接收任务 -->
						<button v-if="current === 0" :disabled="item.status != 0" class="cu-btn bg-blue" @click="receiveTask(item)">
							{{ item.status == 0 ? '接收任务' : '已派发' }}
						</button>
				</view>
				<view style="text-align: right;border-top: 1px solid #efefef;padding-top: 24rpx;" v-if="roleId == '8'&&current!=2">
					<!-- 网格警员接收任务 -->
						<button
							v-if="current === 0 && (item.status == '1' || item.status == '2')"
							:disabled="item.status != 1"
							class="cu-btn bg-blue"
							style="margin-right:10rpx"
							@click.stop="accept(item)"
						>
							{{ item.status == 1 ? '接收' : '已接收' }}
						</button>
						<button v-if="current === 0 && (item.status == '1' || item.status == '4')" :disabled="item.status != 1" class="cu-btn text-red line-red" @click.stop="reject(item)">
							{{ item.status == 1 ? '驳回' : '已驳回' }}
						</button>
						<button v-if="current === 1"  class="cu-btn bg-blue" @click.stop="reportTrouble(item)" style="margin-right:10rpx">隐患上报</button>
						<button v-if="current === 1"  class="cu-btn text-blue line-blue" @click.stop="finishTask(item)">结束任务</button>
				</view>
			</view>
		</view>
		<view class="empty-box" v-else>
			<image src="@/static/img/empty-image.png" class="empty-image"></image>
			<view class="empty-text">暂无数据</view>
		</view>
		<view class="flex flex-direction" style="padding:30rpx" v-if="roleId != '8'"><button class="cu-btn bg-blue lg" @click="fabClick">创建任务</button></view>
		<uni-popup ref="inputDialog" type="dialog">
			<uni-popup-dialog ref="inputClose" mode="input" title="驳回原因" :value="rejectReason" placeholder="请填写驳回原因" @confirm="dialogInputConfirm"></uni-popup-dialog>
		</uni-popup>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			roleId: uni.getStorageSync('roleId'),
			items: ['待接收', '进行中', '已完结'],
			current: 0,
			dataList: [],
			taskTypeOptions: {},
			id:'',
			rejectReason:''
		};
	},
	watch: {
		current: {
			handler(val) {
				this.getTaskList(val);
			},
			immediate: true
		}
	},
	onLoad() {
		this.getTaskTypeOptions();
	},
	methods: {
		getTaskTypeOptions() {
			//获取任务类型
			taskAPI.getSysDict({ type: 'task_type' }).then(res => {
				if (res.code == 200) {
					let obj = {};
					res.data.map(item => {
						obj[item.value] = item.label;
					});
					this.taskTypeOptions = obj;
				}
			});
		},
		getTaskList(status) {
			taskAPI.getTaskList({ status }).then(res => {
				if (res.code == 200) {
					this.dataList = res.data;
				}
			});
		},
		onClickItem(e) {
			if (this.current != e.currentIndex) {
				this.current = e.currentIndex;
			}
		},
		showDetail() {},
		fabClick() {
			//创建任务
			uni.removeStorageSync('checkedArea');
			uni.removeStorageSync('checkedPoliceId');
			uni.removeStorageSync('checkedPoliceName');
			uni.navigateTo({
				url: '../patrolTask/createTask'
			});
		},
		receiveTask(item) {
			//接收派发任务
			uni.removeStorageSync('checkedArea');
			uni.removeStorageSync('checkedPoliceId');
			uni.removeStorageSync('checkedPoliceName');
			uni.navigateTo({
				url: `../patrolTask/distributeTask?id=${item.id}`
			});
		},
		changeTab(index) {
			this.current = index;
		},
		checkDetail(item) {
			//查看详情
			switch (this.current) {
				case 0:
					//待接受
					uni.navigateTo({
						url: `../patrolTask/taskDetail?id=${item.id}`
					});
					break;
				case 1:
					//进行中
					if(this.roleId=='5'){
						uni.navigateTo({
							url: `../patrolTask/policeList?id=${item.id}`
						});
					}else{
						uni.navigateTo({
							url: `../patrolTask/taskDetail?id=${item.id}`
						});
					}
					
					break;
				case 2:
					//已完结
					uni.navigateTo({
						url: `../patrolTask/taskDetail?id=${item.id}&finish=true`
					});
					break;
			}
		},
		accept(item) {
			//网格警员接收任务
			uni.showModal({
				content: '确定接收任务吗？',
				success: rs => {
					if (rs.confirm) {
						let param = {
							id: item.id,
							status: '0'
						};
						taskAPI.gridPoliceApproveTask(param).then(res => {
							if (res.code == 200) {
								uni.showToast({
									icon: 'none',
									title: '已接收',
									success: () => {
										this.getTaskList(this.current);
									}
								});
							}
						});
					}
				}
			});
		},
		reject(item) {
			this.$refs.inputDialog.open()
			this.id=item.id
		},
		dialogInputConfirm(e){
			let param = {
				id: this.id,
				advice:e,
				status: '1'
			};
			taskAPI.gridPoliceApproveTask(param).then(res => {
				if (res.code == 200) {
					uni.showToast({
						icon: 'none',
						title: '已驳回',
						success: () => {
							this.getTaskList(this.current);
						}
					});
				}
			});
		},
		reportTrouble(item){
			//隐患上报
			let userId = uni.getStorageSync('username')
			uni.navigateTo({
				url: `../patrolTask/troubleList?userId=${userId}&id=${item.id}`
			});
		},
		finishTask(item){
			//结束任务
			uni.showModal({
				content: '确定结束任务吗？',
				success: rs => {
					if (rs.confirm) {
						let param = {
							id: item.id,
						};
						taskAPI.gridEndTask(param).then(res => {
							if (res.code == 200) {
								uni.showToast({
									icon: 'none',
									title: '任务已结束',
									success: () => {
										this.getTaskList(this.current);
									}
								});
							}
						});
					}
				}
			});
		}
	}
};
</script>

<style scoped>
.task-box {
	margin-top: 10rpx;
}
.task-item {
	padding: 28rpx;
	margin-bottom: 22rpx;
	background-color: #fff;
	color: #333333;
}
.topTitle {
	line-height: 80rpx;
	font-size: 32rpx;
	border-bottom: 1px solid #efefef;
}
.detail {
	line-height: 50rpx;
	padding: 20rpx 0;
}
.address {
	font-size: 28rpx;
	color: #999999;
	/* line-height: 50rpx; */
}

.last {
	display: flex;
	justify-content: space-between;
}

.finish {
	padding: 28rpx;
	background-color: #fff;
	border-bottom: 1px solid #efefef;
	line-height: 60rpx;
	color: #333333;
}

.btn-box {
	margin-top: 100rpx;
	padding: 0 40rpx 20rpx;
}
.danger-style {
	color: #ee0a24;
	font-size: 24rpx;
}
</style>
